import { useMemo, useCallback } from 'react';
import { useFlexgetAPI } from 'core/api';
import { Method, camelize } from 'utils/fetch';
import { SortBy } from './types';
import { createPluginContainer } from '../base/hooks/api';
const movieToEntry = (movie) => ({
    ...movie,
    entry: camelize(movie.moviesListIds?.reduce((obj, { idName, idValue }) => ({
        ...obj,
        [idName]: idValue,
    }), {
        movieName: movie.title,
        movieYear: movie.year,
    })),
});
function useMovieAPI(url, method) {
    const [state, makeRequest] = useFlexgetAPI(url, method);
    const request = useCallback(async (...args) => {
        const resp = await makeRequest(...args);
        if (resp.ok) {
            resp.data = Array.isArray(resp.data)
                ? resp.data.map(movieToEntry)
                : movieToEntry(resp.data);
        }
        return resp;
    }, [makeRequest]);
    return [state, request];
}
export const MovieListContainer = createPluginContainer(() => {
    return {
        sortByOptions: useMemo(() => [
            {
                value: SortBy.Added,
                label: 'Date Added',
            },
            {
                value: SortBy.Title,
                label: 'Title',
            },
            {
                value: SortBy.URL,
                label: 'URL',
            },
        ], []),
        addEntryProps: useMemo(() => [
            {
                label: 'Movie Name',
                name: 'movieName',
            },
            {
                label: 'Movie Year',
                name: 'movieYear',
                type: 'number',
            },
        ], []),
        api: {
            list: {
                useGet: () => useFlexgetAPI('/movie_list'),
                useAdd: () => useFlexgetAPI('/movie_list', Method.Post),
                useRemove: (listId) => useFlexgetAPI(`/movie_list/${listId}`, Method.Delete),
            },
            entry: {
                useGet: (listId, query) => useMovieAPI(`/movie_list/${listId}/movies?${query}`),
                useAdd: (listId) => useMovieAPI(`/movie_list/${listId}/movies`, Method.Post),
                useRemove: (listId, entryId) => useFlexgetAPI(`/movie_list/${listId}/movies/${entryId}`, Method.Delete),
                useRemoveBulk: (listId) => useFlexgetAPI(`/movie_list/${listId}/movies/batch`, Method.Delete),
            },
        },
    };
});
